.archive h1 span,
.search h1 span {
  padding: 0 10px;
  background: #d9f818; }

.archive .post,
.search .post {
  margin: 5rem 0;
  display: grid;
  grid-gap: 1rem;
  justify-items: center; }
  .archive .post > a,
  .search .post > a {
    width: 100%;
    grid-row: 1 / 6; }
    .archive .post > a img,
    .search .post > a img {
      width: 100%;
      object-fit: cover; }
    .archive .post > a.post-edit-link,
    .search .post > a.post-edit-link {
      grid-row: unset;
      grid-column: 1 / -1; }
  .archive .post .meta,
  .search .post .meta {
    padding: 10px;
    color: white;
    background-color: #333; }
    .archive .post .meta a:hover,
    .archive .post .meta a:focus,
    .archive .post .meta a:active,
    .search .post .meta a:hover,
    .search .post .meta a:focus,
    .search .post .meta a:active {
      color: #e2165d;
      font-style: italic; }
  .archive .post .empty-square,
  .search .post .empty-square {
    width: 100%;
    height: 100%;
    background-color: #d9f818;
    grid-row: 1 / 6; }
  @media only screen and (min-width: 768px) {
    .archive .post,
    .search .post {
      grid-template-columns: 40% 1fr;
      justify-items: left; }
      .archive .post > a img,
      .search .post > a img {
        height: 100%;
        object-fit: cover; } }
  @media only screen and (min-width: 1024px) {
    .archive .post,
